* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
}

body {
	font-family: "微软雅黑";
}

ul {
	list-style: none;
}

a {
	text-decoration: none;
	color: #333;
}

input {
	outline: none;
}

img{
  border: none;
}



